<template>
  <div id="app">
    <nav class="site-nav">
      <router-link
        to="/"
        class="button">Home</router-link>
      <!-- <a
          class="button"
          href="#sidebar">&#8801;</a> -->
      <ul>
        <li class="dropdown sticky">
          <button type="button">Projects</button>
          <ul>
            <li
              v-for="route in projectRoutes"
              :key="route.path">
              <router-link
                :to="route.path"
                @click.native="unfocus">{{ route.title || route.name }}</router-link></li>
          </ul>
        </li>
        <li class="dropdown sticky">
          <button type="button">Notepad</button>
          <ul>
            <li
              v-for="route in notepadRoutes"
              :key="route.path">
              <router-link
                :to="route.path"
                @click.native="unfocus">{{ route.title || route.name }}</router-link></li>
          </ul>
        </li>
        <li><router-link
          to="/about"
          class="button">About</router-link></li>
      </ul>
    </nav>
    <main>
      <router-view />
    </main>
  </div>
</template>

<script>
import routes from './routes'

export default {
  name: 'App',
  data () {
    return {
      sidebarOpen: false
    }
  },
  computed: {
    projectRoutes () {
      return routes.filter(x => x.path.startsWith('/projects/'))
    },
    notepadRoutes () {
      return routes.filter(x => x.path.startsWith('/notepad/'))
    }
  },
  methods: {
    unfocus (event) {
      event.target.blur()
    },
    closeSidebar () {
      this.sidebarOpen = false
    }
  }
}
</script>

<style lang="scss">
.dropdown a.router-link-exact-active,
a.router-link-exact-active.button,
a.router-link-exact-active {
  // TODO this could be better
  color: #000;
}

body {
  padding-top: 0;
}

.site-nav {
  padding: 0.2em;
  ul {
    list-style: none;
    display: inline;
    padding-left: 0;
  }
  li {
    display: inline;
  }
}
</style>



